@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}
html,body{
  background: url(/EDT-FrontTest/static/media/food-wall.a2da7237.jpg);
  
  background-repeat: repeat;
  
}
.App {
  text-align: center;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.container{
  
  margin-top: 5px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  overflow-y: scroll;
}
.hide-me{
  visibility: hidden;
}

h2 {
	font-family: "Dancing Script", cursive;
	font-size: 4rem;
	top: 0;
	margin-bottom: .6%;
	font-weight: bold;
	text-shadow: 4px 4px 6px rgb(255, 255, 255);
  background-color: rgba(11, 204, 185, 0.738);
  border-radius: 40px;
  width: 150vh;
}
#welcome{
  font-size: 5rem;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;
  background-color: rgba(255, 255, 255, 0.879);
  border-radius: 0px;
  margin-bottom: 5px;
}
/* importando fuentes de google */

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Josefin Sans", sans-serif;
}

/* declarando variables de colores */
:root {
	--mainColor: #002fdb;
	--mainColorLight: #00af98e7;
	--secondaryColor: #00f0e8e9;
	--textColor: rgb(17, 16, 22);
}

header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 80px;
	padding: 0 2rem;
	background: linear-gradient(90deg, #67b26f 0%, #4ca2cd 100%);
	color: rgb(17, 16, 22);
	color: var(--textColor);
	font-weight: bold;

}

h1 {
	font-family: "Dancing Script", cursive;
	font-size: 3rem;
	top: 0;
	margin-bottom: .6%;
	font-weight: bold;
	text-shadow: 4px 4px 6px rgb(255, 255, 255);
}

h1:hover {
	color: #002fdb;
	color: var(--mainColor);
}

#app-title {
	text-decoration: none;
	color: rgb(17, 16, 22);
	color: var(--textColor);
}

.no-shadow {
	text-shadow: none;
	margin-top: 3px;
	font-size: 3.2rem;
}

nav a {
	margin: 0 1rem;
	color: rgb(17, 16, 22);
	color: var(--textColor);
	text-decoration: none;
	font-size: 1.3rem;
	text-shadow: 1px 1px 5px white;
}

nav a:hover {
	color: #00f0e8e9;
	color: var(--secondaryColor);
}

header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: rgb(17, 16, 22);
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
}

header div,
nav {
	display: flex;
	align-items: center;
}

/* medidas de pantalla para navbar */
@media only screen and (max-width: 1024px) {
	header .nav-btn {
		visibility:visible;
		opacity: 5;
	}

	header nav {
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		grid-gap: 1.5rem;
		gap: 1.5rem;
		background-color: #00af98e7;
		background-color: var(--mainColorLight);
		transition: 1s;
		-webkit-transform: translateX(-150vh);
		        transform: translateX(-150vh);
		z-index: 50;
	}

	header .responsive_nav {
		-webkit-transform: none;
		        transform: none;
	}

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.5rem;
	}
}
/* contenedor principal de los cards */
.principal-container {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  width: 98rem;
  min-width: 450px;
  min-height: 700px;
  padding: 25px;
  margin: 10px;
  justify-content: flex-start;
  background: rgba(165, 182, 176, 0.7);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(6.5px);
  -webkit-backdrop-filter: blur(6.5px);
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}


/* adaptar a diferentes tamanos de pantallas */
@media only screen and (max-width: 1080px) {
  .cards {
    background-color: aliceblue;
    width: 60vh;
    margin: 20px;
    padding: 10px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .principal-container {
    justify-content: center;
  }
}
@media only screen and (min-width: 1200px) and (max-width: 1550px){
  .cards {
    background-color: aliceblue;
    width: 59vh;
    margin: 20px;
    padding: 10px;
    height: 323px;
  }
  .principal-container {
    justify-content: center;
  }
}


/* cards de la iformacion de los restaurantes */
.cards {
  background-color: aliceblue;
  width: 47.2vh;
  min-height: -webkit-max-content;
  min-height: max-content;
  
  border-radius: 15px;
  margin: 20px;
  padding: 10px;
  box-shadow: 4px 3px 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: black;
}
/* info restaurant */
.info {
  align-items: flex-start;
  justify-content: flex-start;
  margin: 1px 8px 8px;
  margin-left: -1px;
  display: flex;
  flex-direction: column;
  overflow-wrap: anywhere;
  width: 50%;
  text-align: justify;
}
/* titulos restaurant */
.titles {
  font-size: 2.5vh;
  font-weight: bold;
  justify-content: center;
  align-items: flex-start;
  text-align: left;
}

.contact-info {
  margin-top: 15px;
  margin-left: 4px;
  font-size: large;
  text-align: left;
}
.email{
  font-size: 1rem;
}

/* div imagen, rating, botones */
.info2 {
  margin: 1px 8px 8px;
  margin-left: -1px;
  display: flex;
  flex-direction: column;
  overflow-wrap: anywhere;
  width: 50%;
  text-align: justify;
  position: relative;
}

/* contenedor de imagen */
.image {
  width: 100%;
  height: 120px;
}

/* imagen */
.rest-image {
  width: 100%;
  height: auto;
  max-height: 100%;
}

/* div de raiting */
.raiting{
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow-wrap: break-word;
  margin-top: 4%;
}
.raiting-icon{
  color: rgb(255, 183, 0);  
  font-size: 1.8rem;
  justify-content: center;
  align-items: center;
  margin-left: 0;
  text-align: center;
  margin-top: 3%;
}

/* texto Raiting */
#rate-text{
  font-size: 1.2rem;
  font-weight: bold;
  margin: 1px;
  padding: 2px;
}

.social-btns{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content:flex-start;
  align-items:center;
  width: 100%;
  margin: 5px;
  margin-left: 5%;
  position: absolute;
  bottom: 0px;
}

@media only screen and (max-width: 1650px) {
  .social-btns{
    right: 4%;
  }
  
}
